import React, { useState } from "react";
import ads_car_1 from "../../Assets/images/raffle-1.jpg";
import ads_car_2 from "../../Assets/images/raffle-5.jpg";
import ads_car_3 from "../../Assets/images/raffle-6.jpg";
import bnb_coin from "../../Assets/images/raffle-2.jpg";
import bi_ticket from "../../Assets/images/raffle-3.jpg";
import bnbCoin from "../../Assets/images/raffle-4.jpg";
import { Modal } from "react-bootstrap";
import axios from "axios";
import { useEffect } from "react";

import { CopyToClipboard } from "react-copy-to-clipboard";
import referralCodeGenerator from "referral-code-generator";

import Carousel from "react-bootstrap/Carousel";
import { useSelector } from "react-redux";

function CarRaffle() {
  const [index, setIndex] = useState(0);
  const userId = useSelector((state) => state);

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };
  const [modalShow, setModalShow] = useState(false);
  const [showImage, setShowImage] = useState();

  const [lotterydata, setLotterydata] = useState([]);
  const [lotaryId, setLotaryId] = useState();
  const [filteredId, setFilteredId] = useState();
  const [inputLotteryNumber, setInputLotteryNumber] = useState();
  const [loteryIdInApi, setLoteryIdInApi] = useState();

  const [refferalgeneratior, setRefferalgeneratior] = useState();
  const [refferalgeneratior1, setRefferalgeneratior1] = useState();
  const [showLotary, setShowLotary] = useState([]);
  const [lotaryPrice, setLotaryPrice] = useState(0);
  const [allLotaryApi, setAllLotaryApi] = useState([]);
  const [days, setDays] = useState();
  const [hours, setHours] = useState();
  const [minutes, setMinutes] = useState();
  const [seconds, setSeconds] = useState();
  const [newTiem, setNewTiem] = useState(
    new Date("2022-11-22 12:30:00").getTime()
  );
  const now = new Date().getTime();
  const t = newTiem - now + 432000000;
  useEffect(() => {
    const interval = setInterval(() => {
      setDays(Math.floor(t / (1000 * 60 * 60 * 24)));
      setHours(Math.floor((t % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)));
      setMinutes(Math.floor((t % (1000 * 60 * 60)) / (1000 * 60)));
      setSeconds(Math.floor((t % (1000 * 60)) / 1000));
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, [days, hours, minutes, seconds]);

  // countdown time end

  const closeMoal = () => {
    setModalShow(false);
  };
  const handleImageHow = (num) => {
    setIndex(num);
    setModalShow(true);
  };
  useEffect(() => {
    const lotterydata1 = lotterydata.filter(
      (curVal) => curVal.amount == lotaryId
    );

    {
      lotterydata1.map((curVal) => {
        return setFilteredId(curVal.id);
      });
    }
  }, [lotaryId]);

  const fetchLotaryApi = async () => {
    try {
      const response = await axios.get(
        process.env.REACT_APP_URL + "getLotteryDetail"
      );
      setShowLotary(response.data.data);
      setLoteryIdInApi(response.data.data[0].id);
      setLotaryPrice(response.data.data[0].price);
    } catch (err) {
      console.log(err);
    }
  };
  const fetchLotaryApiAll = async () => {
    try {
      const response = await axios.get(
        process.env.REACT_APP_URL +
          `tickets/${loteryIdInApi}/user/${userId.login.user.id}`
      );
      setAllLotaryApi(response.data.data);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    fetchLotaryApi();
  }, []);
  useEffect(() => {
    fetchLotaryApiAll();
  }, [loteryIdInApi]);

  // React.useEffect(() => {
  //   axios
  //     .get(process.env.REACT_APP_URL + "getAllLotteryAmount")
  //     .then((response) => {
  //       setLotterydata(
  //         response.data.data === null || response.data.data === undefined
  //           ? []
  //           : response.data.data
  //       );
  //     });
  // }, []);

  const addTickets = () => {
    axios
      .post(process.env.REACT_APP_URL + "addTicket", {
        // id: "1",
        name: userId.login.user.username,
        lottery_id: loteryIdInApi,
        userId: userId.login.user.id,
        // ticket_number: inputLotteryNumber,
        qty: parseInt(inputLotteryNumber, 10),
      })
      .then((err) => {
        console.log(err);
      });
  };

  const handleRefferal = () => {
    setRefferalgeneratior(
      referralCodeGenerator.custom("lowercase", 11, 8, "vikasSharmaa")
    );
  };

  useEffect(() => {
    setRefferalgeneratior1(refferalgeneratior);
  }, [refferalgeneratior]);
  return (
    <div>
      <section className="ptb_80 pt_sm_50">
        <div className="container">
          <div className="row">
            <div className="col-12 col-md-8 col-lg-8">
              <div className="orangeCard mb-4">
                <div className="row">
                  <div className="col-12 col-md-7">
                    <div
                      id="adsSlide"
                      className="carousel slide"
                      data-ride="carousel"
                    >
                      <ul className="carousel-indicators">
                        <li
                          data-target="#adsSlide"
                          data-slide-to="0"
                          className="active"
                        ></li>
                        <li data-target="#adsSlide" data-slide-to="1"></li>
                        <li data-target="#adsSlide" data-slide-to="2"></li>
                      </ul>

                      <div className="carousel-inner">
                        <div
                          onClick={() => handleImageHow(0)}
                          className="carousel-item active"
                          style={{ cursor: "pointer" }}
                        >
                          <img src={ads_car_1} alt="ads car" />
                        </div>
                        <div
                          onClick={() => handleImageHow(1)}
                          className="carousel-item"
                          style={{ cursor: "pointer" }}
                        >
                          <img src={ads_car_2} alt="ads car" />
                        </div>
                        <div
                          onClick={() => handleImageHow(2)}
                          className="carousel-item"
                          style={{ cursor: "pointer" }}
                        >
                          <img src={ads_car_3} alt="ads car" />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-12 col-md-5">
                    {showLotary.map((curElem) => {
                      return (
                        <div className="" key={curElem.id}>
                          <h5 className="m-0">Lottery Prize</h5>
                          <div className="lotteryPriceNumber">
                            <div className="price_normal">${curElem.price}</div>
                          </div>
                          <div className="mb-3">
                            <i className="fa-solid fa-circle-info"></i> Lottery
                            Breakdown
                          </div>

                          <div className="counterCol">
                            <h5>Countdown to the next draw</h5>
                            <div id="clockdiv">
                              <div>
                                <span className="days" id="day">
                                  {days}
                                </span>
                                <div className="smalltext">Days</div>
                              </div>
                              <div>
                                <span className="hours" id="hour">
                                  {hours}
                                </span>
                                <div className="smalltext">Hours</div>
                              </div>
                              <div>
                                <span className="minutes" id="minute">
                                  {minutes}
                                </span>
                                <div className="smalltext">Minutes</div>
                              </div>
                              <div>
                                <span className="seconds" id="second">
                                  {seconds}
                                </span>
                                <div className="smalltext">Seconds</div>
                              </div>
                            </div>
                            <p>{curElem.description}</p>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>

              <div className="card_Gray2">
                <div className="row row_gap_5">
                  {/* <div className="col-12 mb-3">
                    <h5>
                      <img src={bnb_coin} className="mr-2" /> Enter Car Lottery
                    </h5>
                  </div> */}
                  <div className="col-12 col-md-12">
                    <div className="form-group">
                      <label>Number Of Tickets</label>
                      <input
                        type="number"
                        value={inputLotteryNumber}
                        onChange={(e) => {
                          if (e.target.value.trim().length >= 5) {
                            return false;
                          }
                          setInputLotteryNumber(e.target.value);
                        }}
                        className="form-control"
                        max={1000}
                        id="validationCustom01"
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-12">
                    <div className="form-group">
                      <button
                        type="button"
                        onClick={addTickets}
                        className="btn w-full"
                      >
                        Enter Lottery
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-4 col-lg-4">
              <div className="card_Gray2 mt-5 mt-md-0">
                <div className="">
                  <div className="cardBorder">
                    <h6>My Tickets - {allLotaryApi.length}</h6>
                    <div className="myTicketRow">
                      <div className="myTicketCol">
                        {/* <div className="MT_ic">
                          <img src={bi_ticket} />
                        </div> */}
                        {/* <div className="MT_Count">10</div> */}
                        <div className="MT_Price">
                          ${lotaryPrice * allLotaryApi.length}
                        </div>
                      </div>
                      <div className="">1 Ticket = $ {lotaryPrice}</div>
                    </div>
                  </div>

                  <h6>My Winnings</h6>
                  <div className="myTicketCol">
                    <div className="MT_ic">
                      <img src={bnbCoin.png} />
                    </div>
                    <div className="MT_Count">1000</div>
                    <div className="MT_Price">$325,000</div>
                  </div>
                  <button type="button" className="gry_btn mt-2">
                    Claim
                  </button>

                  <hr />

                  <div className="cardBorder">
                    <h5>Reffer Friend</h5>
                    <ul className="refferFriendList mt-3">
                      <li>
                        <div className="RF_title">Total Refferals</div>
                        <div className="">5</div>
                      </li>
                      <li>
                        <div className="">Total Earnings ($)</div>
                        <div className="">12</div>
                      </li>
                    </ul>
                    <button
                      type="button"
                      className="gry_btn w-full"
                      onClick={handleRefferal}
                    >
                      Copy Reffer al Link
                    </button>
                    <p className="code_genetaror">{refferalgeneratior1}</p>
                    <p className="small mt-2">
                      <i className="fa-solid fa-circle-info"></i> 3% of their
                      purchased amount will go back to you
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Modal
        show={modalShow}
        onHide={closeMoal}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header
          style={{ display: "flex", justifyContent: "space-between" }}
        >
          <Modal.Title id="contained-modal-title-vcenter">Image</Modal.Title>
          <div onClick={closeMoal} style={{ cursor: "pointer" }}>
            X
          </div>
        </Modal.Header>
        <Modal.Body>
          {/* <img
            src={showImage}
            alt=""
            style={{ maxHeight: "67vh", width: "100%" }}
          /> */}
          <Carousel activeIndex={index} onSelect={handleSelect}>
            <Carousel.Item>
              <img
                className="d-block w-100 img-fluid"
                src={ads_car_1}
                alt="First slide"
              />
              <Carousel.Caption></Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
              <img
                className="d-block w-100 img-fluid"
                src={ads_car_2}
                alt="Second slide"
              />

              <Carousel.Caption></Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
              <img
                className="d-block w-100 img-fluid"
                src={ads_car_3}
                alt="Third slide"
              />

              <Carousel.Caption></Carousel.Caption>
            </Carousel.Item>
          </Carousel>
        </Modal.Body>
        <Modal.Footer></Modal.Footer>
      </Modal>
    </div>
  );
}

export default CarRaffle;
